<style scoped lang="stylus" rel="stylesheet/stylus">
.video-uploader
  .video-box
    margin-right -8px
    display flex
    flex-wrap wrap
    .video-item
      position relative
      width 109px
      height 109px
      margin-top 8px
      margin-right 8px
      &:before
        content ''
        position absolute
        left 0
        top 0
        width 100%
        height 100%
        border inherit
        border-color #000
        border-radius inherit
        background #000
        opacity 0.1
      .control
        font-size 32px
        color #fff
        position absolute
        left 50%
        top 50%
        transform translate(-50%, -50%)
      .poster
        width 100%
        height 100%
        object-fit cover
      .deleteBtn
        position absolute
        top -13px*0.5
        right -13px*0.5
    .addBtn
      position relative
      width 109px
      height 109px
      margin-top 8px
      margin-right 8px
      background #EBECF0
      border-radius 6px
      color #969799
      .camera
        font-size 30px
        margin-bottom 7px
      .addBtnText
        font-size 12px
        line-height 18px
        text-align center
</style>

<template>
	<div class="video-uploader">
		<div class="video-box">
			<div
				v-for="(video, index) in videoList"
				class="video-item"
				@click="openVideo(video, index)"
				:key="index"
			>
				<div v-if="playingIndex === index" class="control adv-pause"></div>
				<div v-else class="control adv-play"></div>
				<img class="poster" :src="video.poster" />
				<div v-if="editable" class="deleteBtn jx-mb-cross" @click.stop="deleteVideo(index)"></div>
			</div>
			<slot name="addVideo">
				<div ref="addBtn" class="addBtn flex-col-cen-cen" v-if="addBtnShow" @click="chooseVideo">
					<span class="camera adv-playlist-1"></span>
					<div class="addBtnText">{{ maxUpload }}</div>
					<input
						style="display: none;"
						:key="inputKey"
						ref="inputFile"
						type="file"
						multiple
						accept="video/*"
						@change="videoChoosed"
					/>
				</div>
			</slot>
		</div>
		<van-popup v-model="isVideoShow" :style="{ width: '100%' }" get-container="#app">
			<video-player
				class="video-player vjs-custom-skin"
				ref="videoPlayer"
				:playsinline="true"
				@pause="onPlayerPause($event)"
				@canplaythrough="onPlayerCanplaythrough($event)"
				:options="playerOptions"
			>
			</video-player>
		</van-popup>
	</div>
</template>

<script>
import { videoPlayer } from 'vue-video-player'
import 'vue-video-player/src/custom-theme.css'
import 'video.js/dist/video-js.css'
export default {
	name: 'video-uploader',
	inject: ['videoPoster'],
	model: {},
	props: {
		videos: {
			// 
			type: Array,
			default: () => [
				// {
				//   src: 'http://58.241.11.178:18003/mp/test.mp4',
				//   type: 'video/mp4',
				//   poster: 'https://img01.yzcdn.cn/vant/leaf.jpg'
				// }
			]
		},
		editable: {
			// 
			type: Boolean,
			default: true
		},
		maxUpload: {
			type: Number,
			default: 3
		},
		maxFileSize: {
			type: Number,
			default: 300 // MB
		}
	},
	components: {
		videoPlayer
	},
	data() {
		return {
			inputKey: this.$common.getTimeStampId16(),
			videoList: [
				// 
				// {
				//          src: 'http://58.241.11.178:18003/mp/test.mp4',
				//          type: "video/mp4",
				//          poster: 'https://img01.yzcdn.cn/vant/leaf.jpg',
				// }
			],
			isVideoShow: false, // 
			playingIndex: null, // 
			playerOptions: {
				// 
				playbackRates: [0.5, 1.0, 1.5, 2.0], // 
				autoplay: true, // true,
				muted: false, // 
				loop: false, // 
				preload: 'auto', // <video>auto,
				language: 'zh-CN',
				aspectRatio: '16:9', //  - "16:9""4:3"
				fluid: true, // trueVideo.js player
				sources: [
					{
						type: 'video/mp4', // 
						src: '' // url
					}
				],
				poster: '', // 
				notSupportedMessage: '', // Video.js
				controlBar: {
					timeDivider: true, // 
					durationDisplay: true, // 
					remainingTimeDisplay: false, // 
					fullscreenToggle: true // 
				}
			},
			videoPath: null,
			videoImg: null,
			videoFile: null,
			videoBase64: null,
			dialog: false
		}
	},
	computed: {
		// 
		addBtnShow() {
			const length = this.videoList.length
			return length < this.maxUpload
		}
	},
	created() {
		this.initData()
	},
	watch: {
		isVideoShow(newVal, oldVal) {
			if (!newVal) {
				this.$refs.videoPlayer && this.$refs.videoPlayer.player && this.$refs.videoPlayer.player.pause() // 
				this.playingIndex = null
			}
		},
		videos() {
			this.initData()
		}
	},
	methods: {
		// 
		initData() {
			const videoList = []
			this.videos.map((val, key) => {
				val.src = val.src ? val.src : ''
				// val.type = val.type ? val.type : (this.getFileType(val.src) ? 'video/' + this.getFileType(val.src) : 'video/mp4')
				val.poster = val.poster ? val.poster : ''
				videoList.push(val)
			})
			this.videoList = videoList
		},
		chooseVideo() {
			// this.$refs.inputFile.click()
			this.obtainVideo()
		},
		async videoChoosed() {
			let files = Array.from(this.$refs.inputFile.files || [])
			if (files.length <= 0) return

			files = files.slice(0, this.maxUpload - this.videoList.length)
			const maxSize = this.maxFileSize * 1024 * 1024
			const sizedFiles = files.filter(file => file.size <= maxSize)
			if (sizedFiles.length < files.length) {
				this.$notify({ type: 'warning', message: `${this.maxFileSize}MB` })
			}

			for (const [i, file] of Object.entries(sizedFiles)) {
				const url = URL.createObjectURL(file)
				const image = await this.videoPoster.getPoster(url, +i === sizedFiles.length - 1)
				this.videoList.push({
					file,
					src: url,
					poster: image
				})
			}
			this.refreshInput()
			this.videoChanged()
		},
		refreshInput() {
			this.inputKey = this.$common.getTimeStampId16()
		},
		// 
		openVideo(video, index) {
			// this.playerOptions.sources[0].type = video.type
			this.playerOptions.sources[0].src = video.src
			//      console.log(this.playerOptions.sources[0])
			this.playingIndex = index
			this.isVideoShow = true
			this.$refs.videoPlayer && this.$refs.videoPlayer.player && this.$refs.videoPlayer.player.play()
		},
		// 
		onPlayerPause(player) {},
		// 
		onPlayerCanplaythrough() {
			this.$refs.videoPlayer && this.$refs.videoPlayer.player && this.$refs.videoPlayer.player.play()
		},
		videoChanged() {
			this.$emit('changed', this.videoList)
		},
		// 
		addVideo() {
			//      console.log('addVideo')
			this.$emit('add-video')
		},
		// 
		deleteVideo(index) {
			this.videoList.splice(index, 1)
			this.videoChanged()
			// this.$emit('delete-video', index)
		},
		// 
		getFileType(filePath) {
			var startIndex = filePath.lastIndexOf('.')
			if (startIndex !== -1) {
				return filePath.substring(startIndex + 1, filePath.length).toLowerCase()
			} else return ''
		},
		//video-capture
		obtainVideo() {
			var vuetmp = this

			//
			var permissions = cordova.plugins.permissions
			permissions.checkPermission(
				permissions.WRITE_EXTERNAL_STORAGE,
				function(s) {
					//hasPermission 
					if (!s.hasPermission) {
						//
						//app
						permissions.requestPermission(
							permissions.WRITE_EXTERNAL_STORAGE,
							function(s) {
								if (s.hasPermission) {
									//
								} else {
									vuetmp.$toast.fail('')
								}
							},
							function(error) {}
						)
					} else {
						//
						vuetmp.videoPath = null
						vuetmp.videoImg = null
						vuetmp.videoBase64 = null
						vuetmp.videoFile = null
						var options = { limit: 1, duration: 10 }
						//
						navigator.device.capture.captureVideo(vuetmp.videoCaptureSuccess, error => {}, options)
					}
				},
				function(error) {}
			)
		},
		// 
		videoCaptureSuccess(mediaFiles) {
			this.dialog = true
			var file = mediaFiles[0]
			alert('beforesize:' + file.size)
			var vuetmp = this
			//resolveLocalFileSystemURL()device-absolute-pathEntry
			//jstoURL()url
			resolveLocalFileSystemURL(file.fullPath, function(entry) {
				var fileurl = entry.toURL() //app
				vuetmp.videoPath = fileurl
				// alert('9' + fileurl)
				//
				VideoEditor.transcodeVideo(
					vuetmp.videoTranscodeSuccess, // success cb
					vuetmp.videoTranscodeError, // error cb
					{
						fileUri: fileurl, // the path to the video on the device
						outputFileName: 'ReportVideo', // the file name for the transcoded video
						outputFileType: VideoEditorOptions.OutputFileType.mp4, // android is always mp4
						saveToLibrary: true, // optional, defaults to true
						maintainAspectRatio: true,
						deleteInputFile: false, // optional (android only), defaults to false
						width: 640, // optional, see note below on width and height
						height: 640, // optional, see notes below on width and height
						videoBitrate: 1000000, // optional, bitrate in bits, defaults to 1 megabit (1000000)
						fps: 24, // optional (android only), defaults to 24
						audioChannels: 2, // optional (ios only), number of audio channels, defaults to 2
						progress: function(info) {
							console.log('transcodeVideo progress callback, info: ' + info)
						} // info will be a number from 0 to 100
					}
				)
			})
		},
		//
		videoTranscodeSuccess(result) {
			this.videoPath = result
			var vuetmp = this
			//resolveLocalFileSystemURL'file:///'
			var resulttmp = 'file:///' + result
			resolveLocalFileSystemURL(resulttmp, function(entry) {
				entry.file(function(file) {
					var reader = new FileReader()
					reader.onloadend = function() {
						vuetmp.videoBase64 = this.result //readAsDataURLresult
						// vuetmp.$emit('putFile', vuetmp.videoBase64)
						// vuetmp.dialog = false
						//
						VideoEditor.createThumbnail(vuetmp.createThumbnailSuccess, vuetmp.createThumbnailError, {
							fileUri: result,
							outputFileName: 'ReportVideoThumbnail' + vuetmp.$common.getTimeStampId16(),
							atTime: 2,
							width: 320,
							height: 480,
							quality: 100
						})
					}
					reader.readAsDataURL(file) //Base64
					vuetmp.videoFile = file
					vuetmp.createFilePath(entry.toURL())
					vuetmp.dialog = false
				}, vuetmp.onErrorReadFile)
			})
		},
		createThumbnailSuccess(result) {
			// result is the path to the jpeg image on the device
			console.log('createThumbnailSuccess, result: ' + result)
			this.videoImg = result
			this.videoChoosed2()
		},
		onErrorReadFile(result) {
			console.log('onErrorReadFile, result: ' + result)
		},
		videoChoosed2() {
			alert('aftersize:' + this.videoFile.size)
			this.$set(this.videoList, this.videoList.length, {
				file: this.videoFile,
				src: this.videoBase64,
				poster: this.videoImg
			})
			this.refreshInput()
			this.videoChanged()
		},
		// 
		createFilePath(uri) {
			var _this = this
			window.requestFileSystem(
				LocalFileSystem.PERSISTENT,
				0,
				function(fs) {
					fs.root.getFile(
						'downloadVideo.mp4',
						{ create: true, exclusive: false },
						function(fileEntry) {
							// alert(fileEntry.nativeURL);
							// alert(uri)
							_this.downLoadVideo(uri, fileEntry.nativeURL)
						},
						function(err) {
							console.log(err)
						}
					)
				},
				function(error) {
					console.log(error)
				}
			)
		},
		// fileTransfer plugin
		downLoadVideo(uri, fileURL) {
			var _this = this
			// FileTransfer
			alert('uri:' + uri)
			var fileTransfer = new FileTransfer()
			// download
			alert('fileURL:' + fileURL)
			fileTransfer.download(
				uri, //
				fileURL, //
				function(entry) {
					alert('download complete: ' + entry.toURL())
				},
				function(error) {
					console.log('download error source ' + error.source)
					console.log('download error target ' + error.target)
					console.log('upload error code' + error.code)
				}
			)
		}
	}
}
</script>
