<style scoped lang="stylus" rel="stylesheet/stylus">
.video-previewer
  width 109px
  height 109px
  .poster-box
    position relative
    width 100%
    height 100%
    &:before
      content ''
      position absolute
      left 0
      top 0
      width 100%
      height 100%
      border inherit
      border-color #000
      border-radius inherit
      background #000
      opacity 0.1
    .control
      font-size 32px
      color #fff
      position absolute
      left 50%
      top 50%
      transform translate(-50%, -50%)
    .poster
      width 100%
      height 100%
      object-fit cover
</style>

<template>
	<div class="video-previewer">
		<div class="flex-row-cen-cen poster-box" @click="openVideo">
			<div v-if="isVideoShow" class="control adv-pause"></div>
			<div v-else class="control adv-play"></div>
			<img class="poster" :src="poster" />
		</div>
		<van-popup v-model="isVideoShow" :style="{ width: '100%' }" get-container="#app">
			<video-player
				class="video-player vjs-custom-skin"
				ref="videoPlayer"
				:playsinline="true"
				@pause="onPlayerPause($event)"
				@canplaythrough="onPlayerCanplaythrough($event)"
				:options="playerOptions"
			>
			</video-player>
		</van-popup>
	</div>
</template>

<script>
import { videoPlayer } from 'vue-video-player'
import 'vue-video-player/src/custom-theme.css'
import 'video.js/dist/video-js.css'
export default {
	name: 'video-previewer',
	inject: ['videoPoster'],
	model: {},
	props: {
		src: {
			// 
			type: String,
			default: ''
		},
		poster: {
			// 
			type: String,
			default: ''
		}
	},
	components: {
		videoPlayer
	},
	data() {
		return {
			//      poster: '', // 
			inputKey: this.$common.getTimeStampId16(),
			isVideoShow: false, // 
			playerOptions: {
				// 
				playbackRates: [0.5, 1.0, 1.5, 2.0], // 
				autoplay: true, // true,
				muted: false, // 
				loop: false, // 
				preload: 'auto', // <video>auto,
				language: 'zh-CN',
				aspectRatio: '16:9', //  - "16:9""4:3"
				fluid: true, // trueVideo.js player
				sources: [
					{
						type: 'video/mp4', // 
						src: '' // url
					}
				],
				poster: '', // 
				notSupportedMessage: '', // Video.js
				controlBar: {
					timeDivider: true, // 
					durationDisplay: true, // 
					remainingTimeDisplay: false, // 
					fullscreenToggle: true // 
				}
			}
		}
	},
	computed: {},
	mounted() {},
	watch: {
		isVideoShow(newVal, oldVal) {
			if (!newVal) {
				this.$refs.videoPlayer && this.$refs.videoPlayer.player && this.$refs.videoPlayer.player.pause() // 
			}
		}
	},
	methods: {
		// 
		openVideo() {
			// this.playerOptions.sources[0].type = video.type
			this.playerOptions.sources[0].src = this.src
			//      console.log(this.playerOptions.sources[0])
			this.isVideoShow = true
			this.$refs.videoPlayer && this.$refs.videoPlayer.player && this.$refs.videoPlayer.player.play()
		},
		// 
		onPlayerPause(player) {},
		// 
		onPlayerCanplaythrough() {
			this.$refs.videoPlayer && this.$refs.videoPlayer.player && this.$refs.videoPlayer.player.play()
		}
	}
}
</script>
